import React, {useEffect, useState} from 'react';
import {LockOutlined, UserOutlined} from '@ant-design/icons';
import {Button, Card, Form, Input} from 'antd';
import {Link, useLocation} from "react-router-dom";
import {useAppSelector} from "../../../hooks/redux";
import {useActions} from "../../../hooks/useActions";
import './auth-form.css';
import {RouteNames} from "../../../consts/consts";


const AuthForm: React.FC = () => {
  const location = useLocation();
  const isLoginPage = location.pathname === RouteNames.login;
  const {isLoading, error} = useAppSelector(state => state.userReducer);
  const {signIn, createUser, setUserError} = useActions();
  const [email, setEmail] = useState<string>('');
  const [password, setPassword] = useState<string>('');

  useEffect(() => {
    return () => {
      setUserError('');
    };
  }, []);
  const onFinish = () => {
    if (isLoginPage) {
      signIn({email, password});
    } else {
      createUser({email, password});
    }
  };

  return (
    <div className='form-wrapper'>
      <Card>
        <Form
          name='normal_login'
          className='login-form'
          onFinish={onFinish}
          size='large'
        >
          {error && <div style={{color: 'red', fontSize: '15px'}}>{error}</div>}
          <Form.Item
            name='e-mail'
            rules={[
              {required: true, message: 'E-mail    !'},
              {whitespace: true, message: 'E-mail    !'},
              {type: 'email', message: ' E-mail!'}
            ]}
            hasFeedback
          >
            <Input
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              prefix={<UserOutlined className="site-form-item-icon"/>}
              placeholder='E-mail'
              autoComplete='off'
            />
          </Form.Item>
          <Form.Item
            name='password'
            rules={[
              {required: true, message: '    !'},
              {min: 8, message: '     8 !'}
            ]}
            hasFeedback
          >
            <Input.Password
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              prefix={<LockOutlined className="site-form-item-icon"/>}
              placeholder='Password'
            />
          </Form.Item>

          <Form.Item>
            {isLoginPage
              ? <span className='form-text'> ? <Link to='/register' onClick={() => setUserError('')}>!</Link></span>
              : <span className='form-text'> ? <Link to='/login' onClick={() => setUserError('')}>!</Link></span>
            }
          </Form.Item>

          <Form.Item>
            <Button type='primary' htmlType='submit' className='login-form-button' loading={isLoading}>
              {isLoginPage
                ? ''
                : ''
              }
            </Button>
          </Form.Item>
        </Form>
      </Card>
    </div>
  );
};

export default AuthForm;